/* Desktop Navigation */
.navbar-nav-desktop {
    display: flex;
    gap: 24px;
    justify-content: center;
}

.navbar-menu-btn {
    display: none;
}

/* Mobile Navigation */

.navbar-backdrop {
    display: none;
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Mobile Styles - Below 768px */
@media (max-width: 767px) {
    .navbar-nav-desktop {
        display: none;
    }

    .navbar-menu-btn {
        display: flex;
        background: none;
        border: none;
        cursor: pointer;
        font-size: 28px;
        align-items: center;
        justify-content: center;
        padding: 8px;
        z-index: 600000;
    }

    .navbar-nav-mobile {
        display: flex;
        flex-direction: column;
        z-index: 50000000;
        gap: 10px;
        font-size: 24px;
    }

    .navbar-backdrop {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: fixed;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        padding: 80px;
        z-index: 35;
        background-color: #fdfdfd85;
        backdrop-filter: blur(10px);
        animation: slideDown 0.3s ease-out;
    }
}
